.navbar {
  z-index: 1000;
  line-height: 60px;
  text-align: center;
  padding: 0px !important;
}
.navbar nav {
  flex: 0 0 auto;
  width: calc(99.9% * 1/4 - (15px - 15px * 1/4));
}
.navbar nav:nth-child(1n) {
  margin-right: 15px;
  margin-left: 0;
}
.navbar nav:last-child {
  margin-right: 0;
}
.navbar nav:nth-child(4n) {
  margin-right: 0;
  margin-left: auto;
}
.navbar nav .fa {
  font-size: 1em !important;
}
.navbar nav:nth-child(2) {
  flex: 0 0 auto;
  width: calc(99.9% * 2/4 - (15px - 15px * 2/4));
}
.navbar nav:nth-child(2):nth-child(1n) {
  margin-right: 15px;
  margin-left: 0;
}
.navbar nav:nth-child(2):last-child {
  margin-right: 0;
}
.navbar nav:nth-child(2):nth-child(4n) {
  margin-right: 0;
  margin-left: auto;
}
.navbar nav:nth-child(2) a:hover {
  border-bottom: 2px solid;
  border-color: #1976d2;
  transition: all 0.2s ease;
  color: #222;
}
.navbar nav a {
  color: #333;
  text-transform: uppercase;
  font-weight: 600;
  height: 60px;
}
.navbar nav .fa {
  font-size: 1.5em;
}
.navbar nav #nav-trigger {
  display: none;
}
.navbar nav label[for="nav-trigger"] {
  display: none;
}
.navbar nav ul {
  display: inline-flex;
  margin: 0;
}
.navbar nav ul li:hover ul {
  visibility: visible;
  opacity: 1;
  max-height: 500px;
}
.navbar nav ul li:hover a:first-child:nth-last-child(2):before {
  border-top: 7px solid #0074d9;
  transform: rotate(-180deg);
}
.navbar nav ul li ul {
  display: block;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  background-color: rgba(255,255,255,0.9);
  z-index: 1000;
  max-height: 0;
  transition: 0.2s ease-in-out;
}
.navbar nav ul li a {
  margin: 0 15px;
  border-bottom: 2px solid;
  border-color: transparent;
  line-height: 20px;
  position: relative;
}
.navbar nav ul li a.active {
  border-bottom: 2px solid;
  border-color: #2196f3;
}
.navbar nav ul li a:first-child:nth-last-child(2):before {
  content: "";
  display: inline-block;
  position: absolute;
  height: 0;
  width: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 7px solid #727272;
  top: 37%;
  right: -12px;
  transition: transform 0.2s ease-in-out;
}
@media only screen and (max-width: 1000px) {
  .navbar nav {
    flex: 0 0 auto !important;
    width: calc(99.9% * 1 - (15px - 15px * 1)) !important;
  }
  .navbar nav:nth-child(1n) {
    margin-right: 15px !important;
    margin-left: 0 !important;
  }
  .navbar nav:last-child {
    margin-right: 0 !important;
  }
  .navbar nav:nth-child(undefinedn) {
    margin-right: 0 !important;
    margin-left: auto !important;
  }
  .navbar nav:last-child {
    display: none;
  }
  .navbar nav:nth-child(2) {
    display: none;
  }
  .navbar nav label[for="nav-trigger"] {
    display: block;
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
  }
  .navbar nav ul {
    flex-wrap: wrap;
    padding: 0;
    display: block;
    line-height: 40px;
  }
  .navbar nav ul li ul {
    position: inherit;
    background-color: #2196f3;
  }
  .navbar nav ul li ul a {
    color: #fff;
  }
}
