body {
  margin: 0 10px;
}
.admin-main {
  flex: 0 0 auto;
  width: calc(99.9% * 5/6 - (0px - 0px * 5/6));
  float: right;
}
.admin-main:nth-child(1n) {
  margin-right: 0px;
  margin-left: 0;
}
.admin-main:last-child {
  margin-right: 0;
}
.admin-main:nth-child(0n) {
  margin-right: 0;
  margin-left: auto;
}
.orangeBg {
  background-color: rgba(255,133,27,0.9);
}
.redBg {
  background-color: rgba(209,63,25,0.9);
}
.oliveBg {
  background-color: rgba(61,153,112,0.9);
}
.navyBg {
  background-color: rgba(0,31,63,0.9);
}
.info-box span {
  height: 90px;
  width: 90px;
  line-height: 90px;
  float: left;
  font-size: 2em;
  color: #fff;
  margin: -15px;
}
aside {
  flex: 0 0 auto;
  width: calc(99.9% * 1/6 - (15px - 15px * 1/6));
  position: absolute;
  background-color: #0074d9 !important;
  color: #fff;
}
aside:nth-child(1n) {
  margin-right: 15px;
  margin-left: 0;
}
aside:last-child {
  margin-right: 0;
}
aside:nth-child(6n) {
  margin-right: 0;
  margin-left: auto;
}
aside header {
  display: block;
  text-align: center;
  border-bottom: 1px solid #ddd;
}
aside header img {
  height: 80px;
  margin: 10px;
  border-radius: 50%;
}
aside header ul {
  display: inline-flex;
}
aside header ul li {
  margin: 10px;
  position: relative;
}
aside header ul li:hover span {
  visibility: visible;
}
aside header ul span {
  position: absolute;
  width: 65px;
  background: rgba(0,31,63,0.9);
  line-height: 30px;
  text-align: center;
  visibility: hidden;
  font-size: 0.8em;
  top: 30px;
  left: 50%;
  margin-left: -34px;
  z-index: 999;
}
aside header ul span:after {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -8px;
  width: 0;
  height: 0;
  border-bottom: 8px solid rgba(0,31,63,0.9);
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
}
aside section {
  padding-top: 15px;
}
